import { CSSProperties } from 'react';
import actionJQ from './actionJQ';

export class MyJQ {
  el: HTMLElement[];
  elem: HTMLElement;

  constructor(arg: string | HTMLElement | HTMLElement[]) {
    this.el = this.toArray(arg);
    this.elem = this.el[0];
    this.toArray = this.toArray.bind(this);
  }

  private toArray(arg: string | HTMLElement | HTMLElement[]) {
    let target: HTMLElement[] = [];

    if (typeof arg == 'string' && arg != '') {
      arg.split(',').map((item) => {
        target.push(
          ...(Array.from(
            document.querySelectorAll(item.replace(/^\s*(.+)\s*$/, '$1'))
          ) as HTMLElement[])
        );
      });
    } else if (Array.isArray(arg)) {
      target = arg;
    } else if (arg != '' && arg?.nodeType == 1) {
      target.push(arg);
    }

    return target;
  }

  closest(className: string) {
    return this.el[0].closest(className);
  }

  style(key: keyof CSSProperties, value: string | number) {
    let valueNew = value;

    if (/width|height|left|right|top|bottom/.test(key.toString())) {
      valueNew =
        !/%|px|vh|vw|em|rem|svh|lvh|dvh|svw|lvw|dvw/.test(value.toString()) &&
        +value
          ? value + 'px'
          : value;
    }

    this.el.map((item: any) => {
      item.style[key] = valueNew;
    });
  }

  exceptTarget(className: string) {
    const elements: HTMLElement[] = Array.from(
      document.querySelectorAll(className)
    );
    const newArray: HTMLElement[] = [];
    if (elements.length)
      for (const item of elements) {
        if (item !== this.el[0]) {
          newArray.push(item);
        }
      }
    return actionJQ(newArray);
  }

  focus() {
    this.el[0].focus();
  }

  append(className: string | HTMLElement) {
    const element =
      typeof className == 'string'
        ? document.querySelector(className)
        : className;
    this.el.map((item) => {
      if (element != null) {
        element.append(item);
      }
    });
  }
  prepend(className: string) {
    this.el.map((item) => {
      item.prepend(className);
    });
  }
}
