import { $ } from 'components';
import { BgConf } from './interface.conf';

export function bgWebp({ imgw, imgj, rmClass = '', where }: BgConf) {
  const newImg = new Image();
  newImg.srcset = imgw;
  newImg.onload = () => {
    if (newImg.width > 0 && newImg.height > 0) {
      $(where).style('backgroundImage', `url(${imgw})`);
    } else {
      $(where).style('backgroundImage', `url(${imgj})`);
    }
    $(rmClass).remove('load');
  };
  newImg.onerror = () => {
    $(where).style('backgroundImage', `url(${imgj})`);
  };
}
