import { $ } from 'components/index';
import { useEffect, createRef } from 'react';
import { ImgConf } from './interface.conf';
import './style.scss';

export const ImgPreload: React.FC<ImgConf> = ({
  imgw,
  imgj,
  name,
  rmClass = '',
  style,
}): JSX.Element => {
  const ref = createRef<HTMLDivElement>();

  useEffect(() => {
    const newImg = new Image();
    newImg.srcset = imgw;
    newImg.alt = name;
    newImg.onload = () => {
      $(rmClass).remove('load');

      if (ref.current != null && !ref.current.querySelector('img'))
        if (newImg.width > 0 && newImg.height > 0) {
          $(newImg).append(ref.current);
        } else {
          $(ref.current).style('backgroundImage', `url(${imgj})`);
        }
    };
    newImg.onerror = () => {
      ref.current != null &&
        !ref.current.querySelector('img') &&
        $(ref.current).style('backgroundImage', `url(${imgj})`);
    };
  }, []);

  return <div ref={ref} style={style} className='imgpreload'></div>;
};
